#dv-chat {
  height: 100vh;
  width: 100%;
}

#dv-chat-body {
  height: calc(100vh - 150px);
}

#dv-chat-message {
  margin: 00px 50px;    
  width: calc(100% - 100px);
}

.right {
  text-align: right;
}

.left {
  text-align: left;
}

.balloon {
  display: inline-block;
  /*display: table-cell;*/
  position:relative;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0.5px 0.5px 0.5px 0px #d2d2d2;
  /*min-width: 200px;*/
  max-width: 500px;
}
.balloon:after {
  border-bottom: 0px solid transparent;
  content:"";
  position:absolute;
  top:10px;
}

.my-chat {
  color: rgb(0, 0, 0);
  background: rgb(255, 235, 51);
  text-align: left;
}

.my-chat:after {
  border-top-right-radius: 5px;
  border-top: 15px solid rgb(255, 235, 51);
  border-right: 15px solid transparent;
  border-left: 0px solid transparent;
  right: -15px;
  position: absolute;
  left: 100%;
  text-align: right;
}

.your-chat {
  color: #fff;
  background: #40b7f6;
}

.your-chat:after {
  border-top-left-radius: 5px;
  border-top:15px solid #40b7f6;
  border-left: 15px solid transparent;
  border-right: 0px solid transparent;
  left:-15px;
}

.desc {
  display: table;
}

.text-message {
  /*padding: 10px;*/
  font-size: 14px;
  max-width: 80vw;
}

#dv-chat-message .desc .name {
  font-size: 13px;
  margin: 2px;
}

#dv-chat-message .desc .pic {
}

#dv-chat-message .desc .message-left {
  padding-left: 18px;
}

#dv-chat-message .desc .pic img {
  width: 50px;
  height: 50px;
  border-radius: 22px;
  border: 1px #cecece solid;
}

#dv-chat-message .cell {
  display: table-cell;    
  vertical-align: top;
}
#dv-chat-message .timestamp{
  font-size: 12px;
  padding: 2px 6px;
  vertical-align: bottom;
  /*min-width: 80px;*/
  display: inline-block;
}

#dv-chat-message .float-right {
  float: right;
}

#dv-chat-message .float-left {
  float: left;
}

#dv-chat-message p{    
  clear: both;
  margin: 0px;
}

#dv-chat-message .cell .child {
  margin-top: 10px;
}

#dv-chat-message .cell .child:after {
  border: 0px;
}